import React from 'react'; import styled from 'styled-components'; 

const Content = (props) => (
    <ContentContainer>
        {props.children}
    </ContentContainer>
)

export const ContentContainer = styled.div `
    width: 98%; min-height: 93%; margin: 4px auto; overflow: auto; 
    border-radius: 60px 125px 60px 60px; font-family: 'Baloo 2', cursive; 
    box-shadow: 0px 0px 5px 0px #FEC; z-index: 1; 
    @media (max-width: 700px) { height: 88%; }
    `




export default Content